import React, { useEffect, useState } from 'react'
import { Container, Row, Col } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import './Detail.scss';

function Detail(props){

    useEffect(()=>{ //Component mount, update 작동
        console.log(11111)
        let timer = setTimeout(()=>{
             setAlert(false);
        }, 2000);

        //페이지가 사라질때 실행 함
        //return function dfdfdf(){ 실행할코드 }
        //return ()=>{ 실행할코드 }
        return ()=>{ clearTimeout(timer)} //페이지가 사라질때 타이머 해제

    });

    let { id } = useParams();
    let history = useHistory();//방문기록을 담고있음

    let [alert, setAlert] = useState(true);

    return (
        <Container>
            <div>
                <div className='red'>Detail</div>
            </div>
            {
                alert === true
                ? (
                    <div className='my-alert2'>
                        <p>제고가 얼마 남지 않았습니다.</p>
                    </div>
                )
                : null                    
            }
            <Row>
                <Col md={6}>
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </Col>
                <Col md={6} mt={4}>
                    <h4 className="red pt-5">props.title</h4>
                    <p>상품설명</p>
                    <p>120000원</p>
                    <button className="btn btn-danger">주문하기</button> 
                    <button className="btn btn-primary" onClick={()=>{
                        history.goBack();
                        //history.push('/');
                    }}>뒤로가기</button> 
                </Col>
            </Row>
        </Container>
    )

}

export default Detail;