import React, { useState } from 'react'
import './App.css';
import { Nav, Navbar, Container, NavDropdown, Row, Col, Button } from 'react-bootstrap';
import Data from "./data";

import { Link, Route, Switch } from 'react-router-dom';
import Detail from './Detail';
import axios from 'axios';

function App() {

  let [shoes, setShoes] = useState(Data);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/detail/:id">Detail</Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Switch>
        <Route exact path="/">
          <div className='background'>
            <h1>20% Season Off</h1>
            <p>This is a simple hero unmit, a simple jumbotron-style component for calling extra attention to featured content or information.</p>
            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </div>
          <Container>
            <Row>
              {shoes.map((s, i)=>{
                //console.log('s', s, 'i', i);
                return <Card shoes={s} idx={i} key={i}/>
              })}
            </Row>
          </Container>
        </Route>

        <Route path="/detail/:id">
          <Detail shoes={shoes}/>
        </Route>

        <Route path="/:id">
          <div>세번째 div</div>
        </Route>
      
      </Switch>

    </div>
  );
}

function Card (props) {
  return (
    <Col md={4}>
      <img src={'https://codingapple1.github.io/shop/shoes'+(props.idx+1)+'.jpg'} width='100%'/>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}</p>
    </Col>
  )
}


export default App;